/* 选择图片组件 */
.hermes-photo-picker-header {
  margin-bottom: 15px;
  overflow: hidden;
}
div.hermes-photo-picker-list {
  border: 1px solid #e7e7e7;
  height: 282px;
  overflow-y: scroll;
}
div.hermes-photo-picker-list::-webkit-scrollbar {
  width: 5px;
  background-color: #FBFBFB;
}
div.hermes-photo-picker-list::-webkit-scrollbar-thumb {
  background-color: #C1C1C1;
  border-radius: 3px;
}
.hermes-photo-picker-list-item {
  position: relative;
  display: inline-block;
  border: 1px solid #fff;
  width: 92px;
  height: 92px;
  background-color: #1a1a1a;
  line-height: 90px;
  text-align: center;
  margin: 0 2px;
}
.hermes-photo-picker-list-item img {
  max-height: 90px;
  max-width: 90px;
  margin-bottom: 3px;
  vertical-align: middle;
}
div.hermes-photo-picker-pagination-list {
  padding-bottom: 0;
  height: 282px;
}
.hermes-photo-picker-list-item-icon {
  position: absolute;
  right: 5px;
  bottom: 5px;
  color: #87d068;
  line-height: 14px;
  font-size: 14px;
  z-index: 1;
  background-color: #fff;
  padding: 1px;
  border-radius: 100%;
}
.hermes-photo-picker-upload {
  float: right;
  margin-right: 12px;
}
.hermes-photo-picker-upload .ant-upload-list {
  display: none;
}
div.hermes-photo-picker-footer {
  margin: 15px 0;
  text-align: right;
}
div.hermes-photo-picker-footer .hermes-photo-picker-pagination {
  text-align: right;
  margin-bottom: 8px;
}
div.hermes-photo-picker-footer .hermes-photo-picker-pagination .ant-pagination {
  display: inline-block;
}
div.hermes-photo-picker-footer .notice-info {
  margin-bottom: 10px;
  color: #999;
}
.ant-upload-list-picture-card-readonly.ant-upload-list-picture-card .ant-upload-list-item-info:before {
  display: none;
}
.hermes-checkbox-button-group .ant-btn {
  padding: 4px 8px;
}



